    <html lang="pt-BR">
      <body className={`${inter.variable} ${russoOne.variable} font-sans antialiased bg-[#FFF5F8] text-gray-900 transition-colors duration-500`}>
        {/* O 'children' representa o conteúdo de cada página */}
        {children}
      </body>
    </html>
